import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/page/HelloWorld'
import mypos from '@/components/page/mypos'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'mypos',
      component: mypos
    }
  ]
})



